body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  margin: 0 auto;
  text-align: left;
}

@media (min-width: 650px) {
  #root {
    width: 630px;
  }
  #searchBoxContainer > div {
    width: 630px;
  }
}
@media (min-width: 375px) and (max-width: 649px) {
  #root {
    width: 360px;
  }
  #searchBoxContainer > div {
    width: 360px;
  }
}

@media (min-width: 320px) and (max-width: 374px) {
  #root {
    width: 270px;
  }
  #searchBoxContainer > div {
    width: 270px;
  }
}


@media (max-width: 319px) {
  #root {
    width: 180px;
  }
  #searchBoxContainer > div {
    width: 180px;
  }
}

#imageGrid {
  padding-top: 36px;
}

#searchBoxContainer {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
  background: #fff;
  border-bottom: 1px solid #EBEBEB;
  width: 100%;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

#searchBoxContainer > div {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

#searchBox:focus {
  border-color: #ffb366;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(255, 179, 102, 0.6);
          box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(255, 179, 102, 0.6);
}

#searchBox {
  height: 15px;
  background: #fff;
  border: 1px solid #D9D9D9;
}

#searchBtn {
  background: #3F46AD;
  height: 15px;
  margin-left: 5px;
}

#searchBtn:focus {
  outline: none;
}

#loadingStatus {
  display: none;
}

.thumbnail {
  cursor: pointer;
}

#lightboxOverlay {
  position: absolute;
  top: 0;
  left: 0;

  width:100%;
  height:100%;
  min-height: 100vh;
  background-color: #583753;
  opacity: 0.8;
  z-index: 9999;

  display: none;
}

#lightboxClose {
  position: fixed;
  top: 5px;
  right: 5px;
  z-index: 9999;

  display: none;
}

.lb-close {
  background: url('./images/close.png') top right no-repeat;
  width: 30px;
  height: 30px;
  text-align: right;
  right:0;
  float: right;
  cursor: pointer;
  opacity: 0.7;
}

.lb-close:hover {
  opacity: 1;
}

#lightbox {
  position: absolute;
  top: 100px;
  left: 0;
  width:100%;
  text-align: center;
  z-index: 9999;

  display: none;
}

.lb-outerContainer {
  position: relative;
  margin: 0 auto;
  width: 480px;
  height: 640px;
  background-color: white;
  border-radius: 4px;
}

.lb-container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.lb-image {
  border: 4px solid white;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 30px #333;
          box-shadow: 0 0 30px #333;
  width: 480px;
  height: 640px;
}

.lb-nav {
  position:absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  display:block;
}

.lb-nav a {
  outline: none;
}

.lb-prev {
  background: url('./images/prev.png') left 48% no-repeat;
  width: 34%;
  left: 0;
  float: left;
}

.lb-next {
  background: url('./images/next.png') right 48% no-repeat;
  width: 64%;
  right:0;
  float: right;
}

.lb-prev, .lb-next {
  height: 100%;
  cursor: pointer;
  display: block;
}

.lb-loader {
  position: absolute;
  top: 43%;
  left: 0;
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
  display: none;
}

.lb-cancel {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background: url(./images/loading.gif) no-repeat;
}


.lb-dataContainer {
  position: relative;
  margin: 0 auto;
}

.lb-data .lb-details {
  text-align: left;
  float: left;
  width: 95%;
  padding-left: 4px;
}
.lb-data {
  color: #ccc;
}
.lb-data .lb-caption {
  font-size: 13px;
}

#loadMoreBtn {
  margin-top: 6px;
  border-radius: 4px;
  color: #a1a1a1;
  background: #FFF;
  border: solid #D9D9D9 1px;
  text-decoration: none;
  cursor: pointer;
  margin-bottom: 9px;
}

#loadMoreBtn:hover {
  background: #3f46ad;
  border: solid #3f46ad 1px;
  color: #fff;
  text-decoration: none;
}

#loadingStatus {
  margin-top: 6px;
  color: #a1a1a1;
  background: #FFF;
  text-decoration: none;
  font-size: 11px;
  margin-bottom: 9px;
}


